import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import ContactImage from "../assets/images/contact.svg"
import Phone from "../assets/images/phone.svg"
import Email from "../assets/images/email.svg"
import LinkedIn from "../assets/images/linkedin2.svg"
import GitHub from "../assets/images/github2.svg"
import StackOverflow from "../assets/images/stack-overflow2.svg"

const Contact = () => {
    return (
        <Layout>
            <SEO title="Contact" />
            <div className="contact">
                <div className="row">
                    <div className="column-2">
                        <img src={ContactImage} className="image" />
                    </div>
                    <div className="column-2 info">
                        <label className="title">Let's get in touch</label>
                        <div className="phone">
                            <img src={Phone} alt="panbehkar email" />
                            <span>0936 454 9084</span>
                        </div>
                        <div className="email">
                            <img src={Email} alt="panbehkar email" />
                            <span>abolfazl.panbehkar@yahoo.com</span>
                        </div>
                        <div className="social">
                            <img src={LinkedIn} alt="panbehkar linkedin" />
                            <img
                                src={StackOverflow}
                                alt="panbehkar stackoverflow"
                            />
                            <img src={GitHub} alt="panbehkar github" />
                        </div>
                    </div>
                </div>
            </div>
        </Layout>
    )
}

export default Contact
